body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}

ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.board-row_index {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-left: 15px;
    margin-top: 3px;
}

.index {
    float: left;
    color: #999;
}

.index_ver {
    margin-top: 7px;
    margin-right: 7px;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}

.square:focus {
    outline: none;
}

.square-win {
    color: #d40000;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: flex;
    flex-direction: row;
}

.game-info {
    margin-left: 20px;
}

.moves-list {
    list-style-type: none;
    padding-left: 0;
}

.moves-list li {
    margin-bottom: 10px;
}

.current-move button {
    color: #d40000;
}